


* {
  font-family: Helvetica, Arial, sans-serif;
  background-color: black;
  color: white;
}

body {
  padding: 60px;
}

input {
  font-size: 1.3rem;
  width: 100%;
  color: grey;
}

.card {

 align-content: center;
 border: 1px solid grey;
 margin-bottom: 10px;
 margin-right: 10px;
 overflow: hidden;

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}